<template>
  <div class="content">
    <img src="@/assets/silica_angel_logo.png">
    <div class="message">
      little winds blowing backwards into u over u out of u
    </div>
    <div class="click"
         @click="togglePlaying">
      thankful
    </div>
    <div class="contact">
      <a href="mailto:contact@silicaangel.rip">
        contact@silicaangel.rip
      </a>
    </div>
    <audio ref="audio"
           :src="snippetSrc"
           class="audio"/>
  </div>
</template>

<script>
export default {
  name: 'Home',

  data () {
    return {
      snippetSrc: require('@/assets/snippet.mp3'),
      playing: false
    }
  },

  methods: {
    togglePlaying () {
      this.playing = !this.playing
    }
  },

  watch: {
    playing () {
      if (this.playing) {
        this.$refs.audio.play()
      } else {
        this.$refs.audio.pause()
      }
    }
  }
}
</script>

<style scoped lang="scss">
$color: black;

.content {
  color: $color;
  text-align: center;

  img {
    width: 400px;
    max-width: 80%;
    height: auto;
    margin: auto;
    margin-bottom: 36px;
  }

  .message {
    font-size: 2.75vmax;
  }

  .click {
    font-size: 3.2vmax;
    margin: 32px auto;
  }

  .contact {
    font-size: 2vmax;
    margin: 32px auto;

    a {
      color: inherit;
      text-decoration: none;
    }
  }
}
</style>



